*{
    margin: 0;
    padding: 0;
    border: none;
}

.landing-page{
    width: 1000px;
    height: 500px;
}

.App{
    .app-header{
        width: 100vw;
        height: 5vh;
        background-color: #2d2d2d;
        color: white;
        padding: 0 10px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;

        h1{
            justify-self: left;
            user-select: none;
            cursor: pointer;
        }

        .header-buttons-container{
            width: 250px;
            display: flex;
            justify-content: space-around;
            align-items: center;


            .create-sprite-button{
                width: 100px;
                height: 30px;
                background-color: yellow;
                border-radius: 5px;
                font-size: 15px;
                font-weight: 600;
                cursor: pointer;
                outline: none;

                &:hover{
                    background-color: white;
                }
            }
        }
    }

    .app-main{
        width: 100vw;
        height: 95vh;
        background-color: #282828;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 200px 20px 10px;
        user-select: none;
        position: relative;
        overflow: hidden;

        .tools{
            width: 140px;
            display: flex;
            flex-wrap: wrap;

            .tool-button{
                width: 60px;
                height: 60px;
                background-color: #3a3a3a;
                outline: none;
                cursor: pointer;
                margin: 0 3px 3px 0;
                color: white;
                font-size: 25px;

                &:hover{
                    background-color: #4f4f4f;
                }

                &.active{
                    border: 3px solid yellow;
                    box-sizing: border-box;
                }
            }

            .color-picker{
                width: 95px;
                height: 95px;
                position: relative;
                margin: 50px 0 0 10px;

                .swap-color-button{
                    cursor: pointer;
                }

                .color-button{
                    width: 55px;
                    height: 55px;
                    border: 4px solid #3a3a3a;;
                    position: absolute;
                    outline: none;
                    cursor: pointer;


                    &.main{
                        top: 0;
                        left: 0;
                        z-index: 1;
                    }
    
                    &.secondary{
                        right: 0;
                        bottom: 0;
                    }

                    input{
                        visibility: hidden;
                    }

                    &:hover{
                        border: 4px solid #4f4f4f;
                    }
                } 

                svg{
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    color: #d8d8d8;
                    font-size: 20px;
                    transform: rotate(45deg)
                }
            }
        }

        .frames-controle{
            width: 242px;
            max-height: 800px;
            align-self: flex-start;
            overflow: auto;

            .frame{
                width: 192px;
                height: 192px;
                border-radius: 3px;
                border: 3px solid #444444;
                margin-bottom: 10px;
                background-size: cover;
                position: relative;

                &.active{
                    border: 3px solid yellow !important;
                    position: relative;

                    &::after{
                        content: ''; 
                        position: absolute;
                        right: -23px;
                        top: 40%;
                        border: 10px solid transparent;	
                        border-left: 10px solid yellow;
                    }
                }

                .frame-count{
                    width: 30px;
                    height: 30px;
                    background-color:yellow;
                    border-bottom-right-radius: 3px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-weight: 900;
                    position: absolute;
                    top: 0;
                    left: 0;
                }

                .frame-copy{
                    width: 30px;
                    height: 30px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    font-size: 18px;
                    color: white;
                    background-color: gray;
                    cursor: pointer;
                    outline: none;
                    display: none;
                }

                .frame-delete{
                    width: 30px;
                    height: 30px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    font-size: 18px;
                    color: white;
                    background-color: gray;
                    cursor: pointer;
                    outline: none;
                    display: none;
                }

                &:hover{
                    border: 3px solid #848484;

                    .frame-delete,
                    .frame-copy{
                        display: block;
                    }
                }
            }

            .add-frame-button{
              width: 195px;
              height: 45px;  
              display: flex;
              justify-content: center;
              align-items: center;
              margin-top: 20px;
              border-radius: 3px;
              border: 3px solid #848484;
              background-color: #1d1d1d;
              cursor: pointer;
              outline: none;
              color: white;
              font-size: 16px;

              svg{
                font-size: 25px;
                margin-right: 10px;
              }

              &:hover{
                border: 3px solid #ffd700
              }
            }
        }

        .frame-info{
            width: 80px;
            height: 50px;
            position: absolute;
            top: 5px;
            right: 50%;
            color: yellow;
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
        }

        .main-canvas-container{
            border: 1.5px solid white;
            align-self: center;
            width: 768px;
            height: 768px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: linear-gradient( #d8d8d8, transparent .55px), linear-gradient( 90deg, #d8d8d8, transparent .55px);
            background-size: 24px 24px;
            background-position: center center;
            overflow: hidden;
        }

        .preview-tools{
            width: 384px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-self: flex-start;

            .frames-preview{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-bottom: 50px;
                background-color:#282828;

                .preview-canvas{
                    height: 384px;
                    width: 384px;
                    border: 1px solid white;
                }


                &:fullscreen{
                    justify-content: space-around;
                    flex-direction: row;
                    .preview-canvas{
                        transform: scale(2);
                    }
                }

                .preview-setings{
                    height: 30px;
                    width:  384px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    background-color: #333333;
                    color: white;
                    font-size: 18px;

                    .toggle-skin{
                        width: 35px;
                        background-color: #4c4c4c;
                    }

                    .fps-input{
                        width: 150px;
                        background-color: #4c4c4c;
                    }

                    .fps-count{
                        width: 70px;
                        text-align: center; 
                    }

                    .full-screen-button{
                        height: 30px;
                        width: 35px;
                    }
                }
            }

            .layer-settings{
                width: 320px;
                height: 250px;
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                align-items: flex-start;
                border: 3px solid gray;
                border-radius: 7px;

                .layer-settings-header{
                    height: 50px;
                    width: 320px;
                    border-radius: 1px;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    .add-layer-button{
                        width: 150px;
                        height: 40px;
                        color: black;
                        font-size: 20px;
                        font-weight: bold;
                        background-color: yellow;
                        border-radius: 5px;
                        outline: none;
                        cursor: pointer;

                        &:hover{
                            background-color: white; 
                        }

                        svg{
                            font-size: 26px;
                            margin-left: 8px;
                        }
                    }
                }

                .layer-settings-container{
                    height: 170px;
                    width: 320px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    overflow: auto;
                    

                    .layer-view{
                        width: 260px;
                        height: 50px;
                        margin: 5px 0;
                        background: #3a3a3a;
                        display: flex;
                        justify-content: space-around;
                        align-items: center;
                        color: white;
                        font-size: 22px;
                        border-radius: 5px;
                        box-sizing: border-box;
                        padding: 1px;

                        &.active{
                            border: 1px solid yellow;
                        }

                        .delete-layer-button{
                            width: 30px;
                            height: 30px;
                            color: black;
                            font-size: 18px;
                            background-color: yellow;
                            border-radius: 5px;
                            outline: none;
                            cursor: pointer;

                            &:hover{
                               background-color: white; 
                            }
                        }
                    }
                }
            }
        }
    }
}


.settings{
    display: flex;
    align-items: center;
    width: 200px;
    height: 300px;
    position: absolute;
    top: 50%;
    right: -202px;
    border-radius: 7px;
    transition: .7s;
    background-color: #444444;
    transition-timing-function: cubic-bezier(.18,1.01,.83,1.01);

    &.activeMenu{
        right: 0;
    }

    .exit-menu-button{
        position: absolute;
        top: 0;
        right: 0;
        width: 25px;
        height: 25px;
        border-bottom-left-radius: 7px;
        background-color: yellow;
        font-size: 23px;
        outline: none;
        cursor: pointer;

        &:hover{
            background: white; 
        }
    }

    .settings-area{
        width: 120px;
        height: 220px;
        border: 1px solid yellow;
        border-radius: 7px;
        display: flex; 
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

        button{
            width: 75px;
            height: 35px;
            color: white;
            font-size: 18px;
            border-radius: 10px;
            background: yellow;
            color: black;
            cursor: pointer;
            outline: none;

            &:hover{
               background: white; 
            }
        }
    }

    

    .buttons-container{
        width: 50px;
        height: 261px;
        display: flex;
        flex-direction: column;
        position: relative;
        left: -50px;

        .settings-button{
            width: 50px;
            height: 50px;
            margin-bottom: 2px;
            background-color: #3a3a3a;
            outline: none;
            cursor: pointer;
            color: white;
            font-size: 25px;
            padding-right: 2px;
            display: flex;
            justify-content: flex-start;
            padding-left: 12px;

            &:hover{
                background-color: #4f4f4f;
            }

            &.active{
                border-right: 2px solid yellow;
            }
        }
    }
            
}